/* App.js區塊樣式*/
/* 全局樣式*/
:root {
  --primary-color: #4a4d4f;  /* 藍色 */
  --secondary-color: #8f8ea2;  /* 綠色 */
  --accent-color: #e74c3c;  /* 紅色 */
  --font-large: 24px;
  --font-medium: 18px;
  --spacing-small: 8px;
  --spacing-medium: 16px;
  --spacing-large: 24px;
  --border-radius: 8px;
  --font-family: Arial, sans-serif;
}

/* 全局網站背景圖片設置 */
body {
  background-image: url('bg.jpg'); /* 替換為您的圖片路徑 */
  background-size: cover; /* 讓圖片覆蓋整個視窗 */
  background-position: center; /* 圖片居中顯示 */
  background-repeat: no-repeat; /* 禁止重複 */
  min-height: 100vh; /* 保證至少覆蓋全屏高度 */
  margin: 0; /* 移除默認邊距 */
  font-family: Arial, sans-serif; /* 確保文字樣式正常顯示 */
}
/* 手機設備背景 */
@media (max-width: 768px) {
  body {
    background-image: url('bg.jpg');
  }
}

/* 設置歡迎信息的樣式 */
.welcome-message {
  font-size: 24px; /* 調整文字大小為更大一點 */
  color: #ffffff; /* 設置文字顏色為白色 */
  font-weight: 700;   /* 設置字體粗度 */
  text-align: center; /* 文字居中 */
  margin: 20px 0; /* 添加上下外邊距 */
}

/* 頁面容器 */
.App {
  font-family: var(--font-family);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* 頁面標題 */
.App-header {
  background-color: var(--primary-color);
  padding: var(--spacing-medium);
  color: white;
  text-align: center;
}

/* 主要內容區域 */
.main-content {
  flex-grow: 1;
  padding: var(--spacing-medium);
}

/* 按鈕樣式 */
button {
  padding: var(--spacing-small) var(--spacing-medium);
  background-color: var(--secondary-color);
  color: white;
  border-radius: var(--border-radius);
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s;
  border-width: 1.5px;        /* 設置邊框粗細為 4px */
  border-style: solid;      /* 邊框樣式為實線 */
  border-color: #5c5c5a;    /* 邊框顏色為藍色 */
}

button:hover {
  background-color: var(--accent-color);
}
